<template>
    <div v-if="ready">
        <div class='col-12' style="display:flex;flex-direction:row;">
            <div class='col-3' style='padding-left:0;margin-bottom:1em'>
                <label>Department</label>
                <select class='form-control select' v-model="dept" @change="load_data()">
                    <option v-for="d in dept_list" :value="d.dept_id">{{ d.full_dept }}</option>
                </select>
            </div>            
            <div class='col-2' style='padding-right:0'>
                <label>Fundings</label>
                <select v-model="fund" class='form-control select' @change="load_data()"> 
                    <option value='*'>All</option>
                    <option v-for="f in fundlist">{{ f.name }}</option>
                </select>
            </div>
            <div class='col-5'></div>
            <div class='col-2'>
                <label>Date</label>
                <input type="date" v-model="dd" class="form-control" @change="update_period()"/>
            </div>
            <!--
            <div class='col-2' style='padding-left:0'>
                <label>School Year</label>
                <input type="number" v-model="st" class="form-control" @change="update_period()"/>
            </div>
            <div class='col-2' style='padding-left:0'>
                <label>&nbsp;</label>
                <input type="number" v-model="ed" class="form-control" readonly/>
            </div>
            -->
            <!--
            <div class='col-2' style='padding-left:0'>
                <label>Start Date</label>
                <input type="date" v-model="st" class="form-control" @change="load_data()"/>
            </div>
            <div class='col-2' style='padding-left:0'>
                <label>End Date</label>
                <input type="date" v-model="ed" class="form-control" @change="load_data()"/>
            </div>
            -->
            <!--
            <div class="col-2" style='padding-left:0;padding-right:0'>
                <label>Live Search</label>
                <input type="text" class="form-control" placeholder="Enter Search Key" v-model="key" @keyup="load_data()"/>
            </div>
            -->
        </div>
        <div class='col-12'>
           <table class="table table-striped first-td-padding">
               <thead>
                    <th class='red_th w-6' style='text-align:center'>Asset Category</th>
                    <th class='red_th w-2'>Opening Balance</th>
                    <th class='red_th w-2'>Expense</th>
                    <th class='red_th w-2'>Balance</th>
                </thead>
                <tbody v-for="n in data.length">
                    <tr class='yel_tr' style='cursor:pointer;' @click="change_dp(n-1)">
                        <td class='report_td' style='text-align:center;border-right:2px solid white'>
                            <i class='fa fa-caret-down report' :style="style[n-1]"></i>
                            <b>{{ data[n-1].asset_full }}</b>
                        </td>
                        <td class='report_td' style='border-right:2px solid white;'>
                            <b>HK${{ data[n-1].total }}</b>
                        </td>
                        <td class='report_td' style='border-right:2px solid white;'>
                            <b>HK${{ data[n-1].expense }} ({{ data[n-1].exp_percentage.toFixed(2) }}%)</b>
                        </td>
                        <td class='report_td' style='border-right:2px solid white;'>
                            <b>HK${{ data[n-1].balance }} ({{ data[n-1].bal_percentage.toFixed(2) }}%)</b>
                        </td>
                    </tr>
                    <tr v-if="data[n-1].body" v-show="show[n-1]==true" class='report_tr' v-for="m in data[n-1].body.length">
                        <td class='report_td'>
                            {{ data[n-1].body[m-1].fund }}
                        </td>
                        <td class='report_td'>
                            HK${{ data[n-1].body[m-1].total }}
                        </td>
                        <td class='report_td'>
                            HK${{ data[n-1].body[m-1].expense }} ({{ data[n-1].body[m-1].exp_percentage.toFixed(2) }}%)
                        </td>
                        <td class='report_td'>
                            HK${{ data[n-1].body[m-1].balance }} ({{ data[n-1].body[m-1].bal_percentage.toFixed(2) }}%)
                        </td>
                    </tr>
                </tbody>
           </table>
        </div>
    </div>    
</template>
<script>
export default {
    data(){
        return{
            dd:'',
            style:[],
            show:[],
            fundlist:[],
            fund:'*',
            key:'',
            st:'',
            ed:'',
            dept:'',
            dept_list:[],
            ready:false,
            data:[],
        }
    },
async created(){
    var today = new Date();
    var dd = String(today.getDate()).padStart(2, '0');
    var mm = String(today.getMonth() + 1).padStart(2, '0'); //January is 0!
    var yyyy = today.getFullYear();

    today = yyyy + '-' + mm + '-' + dd;
    this.dd = today;
    await axios.get('Procurement/shared_backend/department.php')
        .then(
            (response)=>{
                this.dept_list = response.data;
                this.dept = this.dept_list[0].dept_id;
            }
        )
    /*
    var tdy = new Date();
    var month = tdy.getMonth();
    month = month+1;
    if(month>7){
        this.st = tdy.getFullYear()+'-08-01';
        this.ed = (tdy.getFullYear()+1)+'-07-31';
    } else {
        this.st = (tdy.getFullYear()-1)+'-08-01';
        this.ed = tdy.getFullYear()+'-07-31';
    }
    */
    await axios.get('Procurement/shared_backend/default_period.php')
        .then(
            (response)=>{
                var st = response.data.st.split("-");
                var ed = response.data.ed.split("-");
                this.st = st[0];
                this.ed = parseInt(ed[0]);
            }
        )
    var formData = new FormData();
    formData.append('dept',this.dept);
    formData.append('st',this.st);
    formData.append('ed',this.ed);
    await axios.post('Procurement/report/backend/dept_fund.php',formData)
        .then(
            (response)=>{
                this.fundlist = response.data;
            }
        )
    await this.load_data();   
    this.ready = true;
},
    methods:{
        async update_period(){
            var year = this.dd.substring(0,4);
            var month = this.dd.substring(5, 7);
            var day = this.dd.substring(8,10);
            if(month < 8){
                this.st = parseInt(year)-1;
                this.ed = parseInt(this.st)+1;
            } else if(month > 7){
                this.st = parseInt(year);
                this.ed = parseInt(this.st)+1;
            }
            this.$forceUpdate();
            await this.load_data();
        },
        change_dp(k){
            if(this.show[k]!=true){
                this.show[k] = true;
                this.style[k] = 'transform: rotate(180deg);';
            } else {
                this.style[k] = 'transform: rotate(0deg);';
                this.show[k] = false;
            }
            this.$forceUpdate();
        },
        async load_data(){
            this.show = [];
            this.style= [];
            var formData = new FormData();
            formData.append('dept',this.dept);
            formData.append('st',this.st);
            formData.append('ed',this.ed);
            formData.append('dd',this.dd);
            formData.append('fund',this.fund);
            await axios.post('Procurement/report/font_render/backend/asset.php',formData)
                .then(
                    (response)=>{
                        if(response.data!='empty'){
                            this.data = response.data;
                        } else {
                            this.data = [];
                        }
                    }
                )   
        }
    }
}
</script>
<style scoped>
    .px0{
        padding-left:0;
        padding-right:0;
    }
    .w-6{
        width:50%;
        vertical-align: top;
    }
    .w-2{
        width:16.67%;
        vertical-align: top;
    }
</style>



